@font-face {
    font-family: "SpaceMono";
    font-weight: 400;
    font-style: normal;
    src: 
    url("../fonts/SpaceMono-Regular.ttf") format("truetype"),
    url("../fonts/SpaceMono-Regular.woff") format("woff"),
    url("../fonts/SpaceMono-Regular.eot?#iefix") format("embedded-opentype");
}

@font-face {
    font-family: "SpaceMono";
    font-weight: 700;
    src: 
    url("../fonts/SpaceMono-Bold.ttf") format("truetype"),
    url("../fonts/SpaceMono-Bold.woff") format("woff"),
    url("../fonts/SpaceMono-Bold.eot?#iefix") format("embedded-opentype");
}
